.container {
  text-align: center;
}
.toggle-switch {
  position: relative;
  width: 75px;
  display: inline-block;
  text-align: left;
  top: 8px;
}
.checkbox {
  display: none;
}
.label {
  display: block;
  overflow: hidden;
  cursor: pointer;
  border: 0 solid rgba(217, 217, 217, 1);
  border-radius: 20px;
  -webkit-tap-highlight-color: transparent;
}
.inner {
  display: block;
  width: 200%;
  margin-left: -100%;
  transition: margin 0.3s ease-in 0s;
}
.inner:before,
.inner:after {
  float: left;
  width: 50%;
  height: 30px;
  padding: 0;
  line-height: 30px;
  color: #fff;
  font-weight: bold;
  box-sizing: border-box;
}
.inner:before {
  content: " ";
  padding-left: 10px;
  background-color: #ffffff;
  color: #fff;
}
.inner:after {
  content: " ";
  padding-right: 10px;
  background-color: rgba(217, 217, 217, 1);
  color: #fff;
  text-align: right;
}
.switch {
  display: block;
  width: 40px;
  height: 40px;
  margin: 5px;
  background: #171632;
  position: absolute;
  top: -10px;
  bottom: 0;
  right: 40px;
  border: 0 solid rgba(217, 217, 217, 1);
  border-radius: 20px;
  transition: all 0.3s ease-in 0s;
}
.checkbox:checked + .label .inner {
  margin-left: 0;
}
.checkbox:checked + .label .switch {
  right: -6px;
}
